body {
	min-width: 320px;
	color: #000;
	background: #e5edf8;
	font-size: 14px;
	line-height: 1.3; }

.out {
	padding: 37px 30px 76px;
	overflow: hidden;
	@include sm {
		padding: 23px 30px 44px; }
	@include r(360) {
		padding-left: 20px;
		padding-right: 20px; } }

.inner {
	position: relative;
	margin: 0 auto;
	max-width: 870px;
	@include sm {
		max-width: 315px; } }

.section {
	@include clr; }

.cloud {
	position: absolute;
	z-index: -1;
	pointer-events: none;
	&_top {
		top: 44px;
		left: -97px;
		@include sm {
			top: 10px;
			left: -66px;
			width: 278px; } }
	&_middle {
		top: 203px;
		right: -54px;
		@include sm {
			top: 230px;
			right: 31px;
			width: 209px; } }
	&_bottom {
		left: 59px;
		bottom: 60px;
		@include sm {
			left: -45px;
			bottom: 140px; } } }
