@font-face {
  font-family: "GT Haptik Trial";
  font-style: normal;
  font-weight: 500;
  src: local("GT Haptik Trial"), local("GT-Haptik-Trial"), url("../fonts/GTHaptikTrial-Medium.woff2") format("woff2"), url("../fonts/GTHaptikTrial-Medium.woff") format("woff");
}

@font-face {
  font-family: "GT Haptik Trial";
  font-style: normal;
  font-weight: 700;
  src: local("GT Haptik Trial"), local("GT-Haptik-Trial"), url("../fonts/GTHaptikTrial-Bold.woff2") format("woff2"), url("../fonts/GTHaptikTrial-Bold.woff") format("woff");
}

@font-face {
  font-family: "GT Haptik Trial";
  font-style: normal;
  font-weight: 500;
  src: local("Muli"), local("Muli"), url("../fonts/Muli.woff2") format("woff2"), url("../fonts/Muli.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  min-width: 320px;
  color: #000;
  background: #e5edf8;
  font-size: 14px;
  line-height: 1.3;
}

.out {
  padding: 37px 30px 76px;
  overflow: hidden;
}

@media only screen and (max-width: 880px) {
  .out {
    padding: 23px 30px 44px;
  }
}

@media only screen and (max-width: 360px) {
  .out {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.inner {
  position: relative;
  margin: 0 auto;
  max-width: 870px;
}

@media only screen and (max-width: 880px) {
  .inner {
    max-width: 315px;
  }
}

.section:after {
  content: " ";
  display: table;
  clear: both;
}

.cloud {
  position: absolute;
  z-index: -1;
  pointer-events: none;
}

.cloud_top {
  top: 44px;
  left: -97px;
}

@media only screen and (max-width: 880px) {
  .cloud_top {
    top: 10px;
    left: -66px;
    width: 278px;
  }
}

.cloud_middle {
  top: 203px;
  right: -54px;
}

@media only screen and (max-width: 880px) {
  .cloud_middle {
    top: 230px;
    right: 31px;
    width: 209px;
  }
}

.cloud_bottom {
  left: 59px;
  bottom: 60px;
}

@media only screen and (max-width: 880px) {
  .cloud_bottom {
    left: -45px;
    bottom: 140px;
  }
}

.header {
  margin-bottom: 53px;
  font-size: 0;
  text-align: center;
}

@media only screen and (max-width: 880px) {
  .header {
    margin-bottom: 13px;
  }
}

.header__logo {
  display: inline-block;
  vertical-align: top;
}

.header__img {
  width: 172px;
  font-size: 14px;
}

@media only screen and (max-width: 880px) {
  .header__img {
    width: 133px;
  }
}

.footer {
  position: relative;
  z-index: 10;
  margin: 55px -19px 0;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 880px) {
  .footer {
    margin-top: 173px;
  }
}

.footer__info, .footer__form {
  float: left;
  padding-left: 19px;
  padding-right: 19px;
}

@media only screen and (max-width: 880px) {
  .footer__info, .footer__form {
    float: none;
  }
}

.footer__info {
  width: 46%;
}

@media only screen and (max-width: 880px) {
  .footer__info {
    margin-bottom: 22px;
    width: auto;
    text-align: center;
  }
}

.footer__form {
  padding-top: 42px;
  width: 54%;
}

@media only screen and (max-width: 880px) {
  .footer__form {
    padding-top: 0;
    width: auto;
  }
}

.footer__label {
  margin-bottom: 12px;
  color: #606C96;
  font-family: "Muli", Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0.7px;
}

@media only screen and (max-width: 880px) {
  .footer__label {
    margin-bottom: 8px;
    font-size: 12px;
    letter-spacing: 0.6px;
  }
}

.footer__title {
  color: #4A86CD;
  font-family: "GT Haptik Trial", Arial, sans-serif;
  font-size: 26px;
  font-weight: 500;
  letter-spacing: 0.43px;
}

@media only screen and (max-width: 880px) {
  .footer__title {
    font-size: 20px;
    letter-spacing: 0.33px;
  }
}

.btn {
  padding: 14px 20px;
  border-radius: 2px;
  color: #fff;
  background: #4785D0;
  font-family: "GT Haptik Trial", Arial, sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 1px;
  text-align: center;
  transition: background 0.25s;
}

.btn:hover {
  background: #5b92d5;
}

.btn:active {
  background: #2e6cb6;
}

.input {
  padding: 0 12px;
  border-radius: 3px;
  width: 100%;
  height: 47px;
  color: #606C96;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  font-family: "Muli", Arial, sans-serif;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.8px;
}

.input::placeholder {
  color: #606C96;
  opacity: 1;
}

.form {
  margin: -5px;
}

.form__field, .form__control {
  float: left;
  padding: 5px;
}

@media only screen and (max-width: 880px) {
  .form__field, .form__control {
    float: none;
  }
}

.form__field {
  width: 59%;
}

@media only screen and (max-width: 880px) {
  .form__field {
    width: 100%;
  }
}

.form__control {
  width: 41%;
}

@media only screen and (max-width: 880px) {
  .form__control {
    width: 100%;
  }
}

.form__btn {
  width: 100%;
}

.info {
  float: left;
  width: 57%;
}

@media only screen and (max-width: 880px) {
  .info {
    width: 100%;
  }
}

.info__title {
  margin-bottom: 19px;
  color: #4A86CD;
  font-family: "GT Haptik Trial", Arial, sans-serif;
  font-size: 70px;
  font-weight: 700;
  line-height: 72px;
  letter-spacing: 1.17px;
}

@media only screen and (max-width: 880px) {
  .info__title {
    margin-bottom: 20px;
    font-size: 55px;
    line-height: 62px;
    letter-spacing: 0.92px;
  }
}

@media only screen and (max-width: 360px) {
  .info__title {
    font-size: 46px;
    line-height: 1.2;
  }
}

.info__description {
  max-width: 382px;
}

@media only screen and (max-width: 880px) {
  .info__description {
    max-width: 167px;
  }
}

.info__description p {
  color: #3C3C48;
  font-family: "Muli", Arial, sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: 0.57px;
}

.figure {
  position: relative;
  float: right;
  width: 43%;
  height: 265px;
  pointer-events: none;
}

@media only screen and (max-width: 880px) {
  .figure {
    width: 100%;
    height: 0;
  }
}

.figure__main {
  position: absolute;
  left: 0;
  bottom: -196px;
  max-width: 91%;
}

@media only screen and (max-width: 880px) {
  .figure__main {
    top: 82px;
    left: 180px;
    bottom: auto;
    width: 271px;
    max-width: none;
  }
}

.figure__decor {
  position: absolute;
}

.figure__decor_top {
  top: -82px;
  right: 16px;
  max-width: 23%;
}

@media only screen and (max-width: 880px) {
  .figure__decor_top {
    top: 80px;
    right: -130px;
    max-width: none;
    width: 66px;
  }
}

.figure__decor_bottom {
  right: 100%;
  bottom: -46px;
  margin-right: -16px;
  max-width: 38%;
}

@media only screen and (max-width: 880px) {
  .figure__decor_bottom {
    top: 355px;
    right: 90px;
    bottom: auto;
    margin-right: 0;
    width: 112px;
  }
}
