//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $family: 'Roboto') {
	font-family: $family;
	font-style: $style;
	font-weight: $weight; }

$gt-haptik-trial: 'GT Haptik Trial';
$muli: 'Muli';

//light
@font-face {
	@include fontdefault(normal, 500, $gt-haptik-trial);
	@include fontpath($gt-haptik-trial, 'GT-Haptik-Trial', GTHaptikTrial-Medium); }
@font-face {
	@include fontdefault(normal, 700, $gt-haptik-trial);
	@include fontpath($gt-haptik-trial, 'GT-Haptik-Trial', GTHaptikTrial-Bold); }
@font-face {
	@include fontdefault(normal, 500, $gt-haptik-trial);
	@include fontpath($muli, $muli, Muli); }
